  timePickerDefaultToolbarTitle: ' ',
  dateRangePickerDefaultToolbarTitle: '  ',
  // Clock labels
  clockLabelText: (view, time, adapter) => `${views[view]} . ${time === null ? '  .' : `   ${adapter.format(time, 'fullTime')}.`}`,
